body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.chart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 55px;
  width: 190px;
}
.text {
  color: black;
  font-size: 13px;
  /* display: flex;
  flex-direction: column; */
}
.text > p {
  margin: 0;
  text-align: left;
}
.symbol {
  color: #0081f2;
  font-weight: 700;
}
.price {
  font-weight: 700;
}
.change {
  font-size: 11px;
}
.positive {
  color: #093;
}
.negative {
  color: #ff333a;
}
.canvas {
  width: 100px;
  height: 35px;
}

.App-header {
  height: 40px;
  margin: 2px 5px;
  position: relative;
}


.search-bar {
  width: 100%;
  height: 100%;
  /* max-width: 500px; */
  display: block;
  /* margin: 0; */
  padding: 0 5px;
  border: none;
  box-sizing: border-box;
}

.search-options {
  display: none;
  position: absolute;
  top: calc(100% + 1px);
  left: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  /* max-width: 500px; */
}

.search-bar:focus + .search-options {
  display: unset;
}


.search-options > li {
  height: 40px;
}

.search-options button {
  height: 100%;
  width: 100%;
  display: block;
  background: white;
  border: none;
  cursor: pointer;
  /* padding: 0; */
  text-align: left;
}

.search-options button:hover {
  background: #f0f0f0;
}

.charts-container {
  margin: 10px 10px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
